body {
    margin: 0px;
    padding: 0px;
    font-family: 'Karla', sans-serif;
    overscroll-behavior:none;
}

.margin {
    margin: 0px 200px 0px 200px;
}

.padding {
    padding: 50px 50px 50px 50px;
}

.dummyText {
    width: 100%;
    max-width: 300px;
}

.links {
    text-decoration: none;
    color: black;
}

.navMenu {
    display: none;
}

.navbar {
    font-size: 1.3rem;
    margin-bottom: 80px;
}

.navbar-nav {
    box-sizing: border-box;
    position: fixed;
    width: 100vw;
    top: 0px;
    left: 0;
    right: 0;
    padding: 10px 50px 10px 50px;
    background-color: white;
    /*************/
    z-index: 1;
 
}

.brandLogo {
    font-family: 'Poppins', sans-serif;
    font-size: 3rem;
    font-weight: bolder;
}

.navbarItems {
    display: flex;
    justify-content: space-between;
}

.navItem-list {
    list-style: none;
    display: flex;
}

.navItems-center {
    display: flex;
    justify-content: center;
}

.navItem {
    margin-inline-end: 20px;
}

.navItem:hover {
    position: relative;
    bottom: 5px;
}

.navItems-corner {
    margin-left: auto;
}

.mobileNav {
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
}

.topSection {
    background-color: #1c5f4ee3;
}

.topSectionItem {
    display: flex;
}

.introImage {
    padding: 50px;
}

#topHeadline {
    font-size: 3rem;
    color: white;
    font-weight: bolder;
    max-width: 500px;
}

#introText {
    color: #97b8af;
    font-size: 17px;
    margin-top: 50px;
    max-width: 400px;
}


.btn {
    font-size: 16px;
    padding: 15px 30px 15px 30px;
    border-radius: 25px;
    border: none;
    font-weight: bold;
    position: relative;
    top: 0;
    transition: top ease 0.5s;
}

.btnLinks {
    text-decoration: none;
    color: black;
}

.btn:hover {
    top: -5px;
}

#shopBtn {
    background-color: rgb(243, 194, 70);

}

#exploreBtn1 {
    background-color: #1c5f4e00;
    color: white;
    border: solid rgb(189, 189, 189)2px;
    margin-left: 20px;
}

#exploreBtn1:hover {
    border: solid white 2px;
}

.topSectionBtns {
    display: flex;
    margin-top: 30px;
}

#midSectionHeadline {
    font-size: 2rem;
}

.midItem {
    margin-bottom: 35px;
    margin-right: 0px;
    width: 100%;
    max-width: 300px;
}

.midSectionItems {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 300px auto;
    column-gap: 50px;
}

.heroProducts {
    display: flex;
    overflow-x: scroll;
    padding: 10px;
    width: 1100px;
}

.heroProductSection {
    display: flex;
    align-items: center;
}

.heroSecItem {
    margin-inline-end: 50px;
}

.cardItem {
    text-align: center;
    border-radius: 10px 10px;
    margin-inline-end: 60px;
    margin-top: 50px;
    border: 2px solid black;
    width: 280px;
    height: 220px;
    overflow: hidden; 
    /* position: relative;
    top: 0px;
    transition: top ease 0.5s; */
}

.cardItem:hover {
    top: -10px
}

