#productCard2{
    background-color: gray;
}

#exploreBtn2 {
    background-color: black;
    color: white;
}

.aboutSection {
    background-color: rgba(209, 209, 209, 0.24);
}

.columnGrid1 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 50px;
    align-items: center;
    z-index: -1;
}

.uspIcon {
    z-index: -1;
    font-size: 2rem;
    filter: drop-shadow(3px 2px 0px rgba(0, 0, 0, 0.322));
}

#simpleImg {
    width: 100%;
    max-width: 500px;
    border-radius: 25px;
    float: right;
}

#moreInfoImg {
    width: 100%;
    max-width: 900px;
    border-radius: 25px;
}

.columnGrid2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 40px;
}

.blogThumbnail {
    width: 100%;
    max-width: 350px;
    height: 300px;
}

.blog {
    text-align: justify;
}

.blogs {
    background-color: white;
    padding: 20px;
    border: solid 2px black;
    width: 100%;
    max-width: 350px;
    margin-bottom: 30px;
    filter: drop-shadow(10px 8px 0px black);
    position: relative;
    top: 0;
    transition: top ease 0.5s;
    z-index: 0;
}

.blogs:hover {
    top: -25px;
}

.blogsRow {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 50px;
    justify-content: center;
    margin-bottom: 100px;
}

.blogContainer {
    display: flex;
    justify-content: center;
    margin-top: 50px;
    box-sizing: border-box;

}

.footer {
    background-color: #92af9249;
}

#mailInput {
    width: 300px;
    padding: 10px;
    border-radius: 10px;
}

#emialIcon {
    font-size: 30px;
}

#emailBtn {
    color: white;
    background-color: rgb(19, 94, 0);
    border: solid black 1px;
    padding: 3px 5px 3px 5px;
    border-radius: 10px;
    margin-left: 10px;
}

.subscriptionBoxHead {
    margin-top: -25px;
    margin-bottom: 8px;
}

.footerLinkColumns {
    list-style: none;
    text-decoration: none;
}

.footerLinkItems {
    display: grid;
    grid-template-columns: repeat(4, 120px);
    column-gap: 20px;
}

.footerLinks {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}

.subscriptionBox {
    display: flex;
}

.footerBrandName {
    font-family: 'Poppins', sans-serif;
    font-size: 40px;
    font-weight: bold;
}

.logos {
    width: 1.2rem;
    margin-inline-end: 10px;
}

.footerLink:hover {
    text-decoration: underline;
}

.footerEnd {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}

.footerImg {
    position: relative;
    float: right;
    bottom: 150px;

}

#chairImg {
    width: 300px;
    /* position: relative;
    bottom: 100px; */
    /* filter: drop-shadow(5px 30px 50px rgba(0, 0, 0, 0.178)); */
}

.prodPgTopSec {
    text-align: center;
    /* margin-bottom: 250px; */

}

.prodNavBarItemsList{
    display: flex;
    background-color:black; 
    /* #1c5f4ee3; */
    padding: 0px 50px;
    font-size: 18px;
    color: white;
    position: relative;
    top:20px
}

.prodNavBarItem{
    margin-inline-end: 20px;
    padding: 20px 10px;

}

.prodNavBarItem:hover{
    background-color:#1c5f4ee3; 
}

#prodNavBar1:active{
    background-color:#1c5f4ee3; 
}

.productPgHead {
    font-size: 3rem;
    font-weight: bold;
    color: rgb(5, 5, 5);
}

.productPgContainer {
    /* background-color: #1c5f4ee3; */
    background-color: rgba(209, 209, 209, 0.24);
}

.mainProdSection{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 200px;
    /* border-end-end-radius:10px: */
}

.contactContainer {
    display: flex;
    justify-content: center;
    /* background-color: #92af924b; */
    border-radius: 10px;

}

.contactForm {
    margin-top: 150px;
    margin-bottom: 250px;
    background-color: #97b8af3d;
    display: flex;
    justify-content: center;
    padding: 100px;
    width: 600px;
    border-radius: 10px;
    border: solid 2px black;
    box-shadow: 8px 6px rgb(0, 0, 0);
}

#contactFormBtn {
    background-color: black;
    color: white;
    margin-top: 30px;
}

.contactInputs {
    border-radius: 5px;
    width: 275px;
    height: 25px;
    border: solid black 1px;
    margin-bottom: 20px;
}

#fName {
    margin-right: 40px;

}

#emailInput {
    width: 600px;
}

.formContainer1 {
    display: flex;
}

.textArea1 {
    resize: vertical;
    min-width: 600px;
    min-height: 200px;
    border-radius: 10px;
    border: solid black 1px;

}

.loginBox {
    display: grid;
    grid-template-columns: 40% 60%;

    /* flex-basis: 40% 60%; */
    /* padding: 200px; */

}

.loginHead {
    letter-spacing: 20px;
    filter: drop-shadow(7px 2px 1px rgba(175, 175, 175, 0.829));
}

#loginBtn {
    border: 2px solid black;
}

.loginContainer {
    display: flex;
    justify-content: center;
    height: 950px;
    align-items: center;
}

.loginPgPic {
    background-color: #1c5f4ee3;
}

.brandName {
    position: fixed;
    padding: 50px;
}

.loginItem {
    width: 400px;
    height: 25px;
    margin-bottom: 25px;
}

@media screen and (max-width:1580px) {

    .padding {
        padding: 75px 30px 75px 30px;
    }
}

@media screen and (max-width:1340px) {
    .margin {
        margin: 0px 100px 0px 100px;
    }

    .padding {
        padding: 50px 25px 50px 25px;
    }
}

@media screen and (max-width:1200px) {
    .margin {
        margin: 0px 100px 0px 100px;
    }

    .padding {
        padding: 50px 25px 50px 25px;
    }
}

@media screen and (max-width:992px) {

    /*medium Devices*/
    .margin {
        margin: 0px 50px 0px 50px;
    }

    .blogsRow {
        overflow-x: scroll;
        justify-content: start;
        margin-bottom: 100px;
        padding: 50px 50px 0px 50px;

    }

    .blogs {
        width: 300px;
    }

    .columnGrid2 {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }

    .columnGrid1 {
        margin-bottom: 50px;
    }

    #simpleImg {
        width: 400px;
        border-radius: 25px;
        float: unset;
    }

    #moreInfoImg {
        width: 450px;
        border-radius: 25px;
    }


}

@media screen and (max-width:576px) {
    .margin {
        margin: 0px 10px 0px 10px;
    }

    .padding {
        padding: 50px 25px 50px 25px;
    }

    .navbar {
        padding: 20px 25px 20px 25px;
    }

    .navMenu {
        display: block;
    }

    .navbarItems {
        display: block;
    }

    .navItem-list {
        display: block;
    }

    .navItem {
        margin-inline-end: 0px;
        margin-bottom: 20px;
    }

    .navItems-corner {
        text-align: center;
        margin-top: -40px;
    }

    .mobileNav {
        display: none;
        box-sizing: border-box;
    }

    .xsNavItems {
        display: flex;
        justify-content: space-between;
    }

    #navMenuBtn {
        font-size: 2.5rem;
        padding-top: 15px;
    }

    #topHeadline {
        font-size: 2.5rem;
    }

    .introImage {
        display: none;
    }

    .midSectionItems {
        display: block;
        width: 280px;
    }

    .midItem {
        width: 500px;
    }

    .footerLinkItems {
        grid-template-columns: repeat(2, 120px);
    }

    .footerEnd {
        flex-wrap: wrap;
    }

    .blogsRow {
        overflow-x: scroll;
        justify-content: start;
        width: 350px;
        margin-bottom: 100px;
        padding: 20px 20px 20px 20px;
    }

    .blogs {
        width: 300px;
    }

    .blogs:hover {
        top: -10px
    }

    .subscriptionBox {
        justify-content: center;
    }

    #chairImg {
        display: none;
    }
}

@media screen and (max-width:400px) {

    .padding {
        padding: 20px;
    }

    #mailInput {
        width: 200px;
    }

    #simpleImg {
        width: 350px;
    }

    #moreInfoImg {
        width: 400px;
    }

    .subscriptionBox {
        margin-top: 20px;
    }
}